import { APP_BASE_HREF, registerLocaleData } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';

import localeES from '@angular/common/locales/es';
import { LOCALE_ID, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { AccordionModule, AlertModule, ModalModule, TypeaheadModule } from 'ngx-bootstrap';
import { MomentModule } from 'ngx-moment';
import { environment } from '../environments/environment';
import { ApiService } from './api.service';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ProductInfoModalComponent } from './product-info-modal/product-info-modal.component';
import { ProductSearchSelectModalComponent } from './product-search-select-modal/product-search-select-modal.component';
import { KeyValueSortByPipe } from './sort-by.pipe';
import { AppEffects } from './store/app.effects';
import { DietEffects } from './store/diet/diet.effects';
import * as fromDiet from './store/diet/diet.reducer';
import { UserDataEffects } from './store/userData/userData.effects';
import * as fromUserData from './store/userData/userData.reducer';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';

registerLocaleData(localeES, 'es');

@NgModule({
  declarations: [
    AppComponent,
    ProductSearchSelectModalComponent,
    ProductInfoModalComponent,
    KeyValueSortByPipe,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    AccordionModule.forRoot(),
    AlertModule.forRoot(),
    ModalModule.forRoot(),
    TypeaheadModule.forRoot(),
    FormsModule,
    HttpClientModule,
    MomentModule.forRoot(),
    StoreModule.forRoot({ userData: fromUserData.reducer, diet: fromDiet.reducer }, {}),
    EffectsModule.forRoot([AppEffects, UserDataEffects, DietEffects]),
    StoreDevtoolsModule.instrument(
      { maxAge: 50, logOnly: false }
    ),
    ReactiveFormsModule,
    CalendarModule.forRoot({ provide: DateAdapter, useFactory: adapterFactory })
  ],
  providers: [
    { provide: APP_BASE_HREF, useValue: environment.baseHref },
    { provide: LOCALE_ID, useValue: 'es' },
    ApiService,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {
}
