import { Action, createReducer, on } from '@ngrx/store';
import { carbsKCalToGrams, fatKCalToGrams, getRatioByWeightAndAmount } from '../../../utils/calculator';
import { IUserData } from '../../api.service';
import { initialState } from '../app.state';
import {
  saveUserDataSuccess,
  updateActivityIndex,
  updateAge,
  updateBodyFatPercentage,
  updateCarbsRatio,
  updateFatRatio,
  updateGender,
  updateGoal,
  updateHeight,
  updateProteinRatio,
  updateWeight,
  userDataLoadSuccess
} from './userData.actions';
import { selectGoalCalories, selectGoalCarbsKCal, selectGoalFatKCal, selectGoalProteinKCal, selectLeanBodyMass } from './userData.selectors';

const userDataReducer = createReducer(initialState.userData,
  on(updateHeight, (state, action) => {
    return {
      ...state,
      height: action.value,
      pendingChanges: true,
    };
  }),
  on(updateWeight, (state, action) => {
    const updatedState = { ...state, weight: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal));
    return {
      ...state,
      weight: action.value,
      carbsRatio,
      pendingChanges: true,
    };
  }),
  on(updateActivityIndex, (state, action) => {
    const updatedState = { ...state, activityIndex: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal));
    return {
      ...state,
      activityIndex: action.value,
      carbsRatio,
      pendingChanges: true,
    };
  }),
  on(updateBodyFatPercentage, (state, action) => {
    const updatedState = { ...state, bodyFat: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal));
    return {
      ...state,
      bodyFat: action.value,
      carbsRatio,
      pendingChanges: true,
    };
  }),
  on(updateAge, (state, action) => ({ ...state, age: action.value, pendingChanges: true, })),
  on(updateGoal, (state, action) => {
    const updatedState = { ...state, goal: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal));
    return { ...state, goal: action.value, carbsRatio, pendingChanges: true, };
  }),
  on(updateProteinRatio, (state, action) => {
    const updatedState = { ...state, proteinRatio: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal));
    return { ...state, proteinRatio: action.value, carbsRatio };
  }),
  on(updateCarbsRatio, (state, action) => {
    const updatedState = { ...state, carbsRatio: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const carbsKCal = selectGoalCarbsKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const fatRatio = getRatioByWeightAndAmount(leanBodyMass, fatKCalToGrams(goalCalories - proteinKCal - carbsKCal));
    return { ...state, carbsRatio: action.value, fatRatio, pendingChanges: true, };
  }),
  on(updateFatRatio, (state, action) => {
    const updatedState = { ...state, fatRatio: action.value };
    const fakeParentState = { userData: updatedState, diet: null, pdfBlob: null };
    const goalCalories = selectGoalCalories(fakeParentState);
    const proteinKCal = selectGoalProteinKCal(fakeParentState);
    const fatKCal = selectGoalFatKCal(fakeParentState);
    const leanBodyMass = selectLeanBodyMass(fakeParentState);
    const carbsRatio = +getRatioByWeightAndAmount(leanBodyMass, carbsKCalToGrams(goalCalories - proteinKCal - fatKCal)).toFixed(2);
    return { ...state, fatRatio: action.value, carbsRatio, pendingChanges: true, };
  }),
  on(userDataLoadSuccess, (state, action) => {
    return { ...state, ...action.userData };
  }),
  on(updateGender, (state, action) => {
    return { ...state, gender: action.value, pendingChanges: true, };
  }),
  on(saveUserDataSuccess, ((state, action) => {
    return { ...state, pendingChanges: false };
  }))
);


export function reducer(state: IUserData | undefined, action: Action) {
  return userDataReducer(state, action);
}
