import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'keyValueSortBy'
})
export class KeyValueSortByPipe implements PipeTransform {

  transform(value: { key: number | string, value: any }[], order = '', column: string = ''): unknown {
    if ( !value || order === '' || !order ) {
      return value;
    } // no array
    if ( value.length <= 1 ) {
      return value;
    }

    if ( !column ) {
      return value.sort((a, b) => {
        if ( order === 'asc' ) {
          return ('' + a.key).localeCompare('' + b.key);
        }
        return ('' + b.key).localeCompare('' + a.key);
      });
    }

    return value.sort((a, b) => {
      if ( order === 'asc' ) {
        return ('' + a.value[column]).localeCompare('' + b.value[column]);
      }
      return ('' + b.value[column]).localeCompare('' + a.value[column]);
    });
  }

}
