import { TypedAction } from '@ngrx/store/src/models';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

export interface Correllatable {
  correlationId: string;
}

export type CorrelatableAction = TypedAction<string> & { correlationId: string };

export function filterCorrelatedId(correlatedId: string) {
  return (source: Observable<any>) => {
    return source.pipe(
      map(x => x as CorrelatableAction),
      filter(x => x.correlationId === correlatedId)
    );
  };
}
