import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { IProduct } from '../api.service';

@Component({
  selector: 'app-product-info-modal',
  templateUrl: './product-info-modal.component.html',
  styleUrls: ['./product-info-modal.component.css']
})
export class ProductInfoModalComponent implements OnInit {
  qty: number;
  get product(): IProduct {
    return this._product;
  }
  set product(value: IProduct) {
    this._product = value;
    this.productInfo = this.getProductInfo();
  }

  properties = {
    kCalories: 'kCal',
    protein: 'protenas',
    carbohydrates: 'carbohidratos',
    fat: 'grasas',
    vitA: 'Vitamina A',
    vitB1: 'Vitamina B1',
    vitB2: 'Vitamina B2',
    vitB3: 'Vitamina B3',
    vitB5: 'Vitamina B5',
    vitB6: 'Vitamina B6',
    vitB9: 'Vitamina B9',
    vitB12: 'Vitamina B12',
    vitC: 'Vitamina C',
    vitD: 'Vitamina D',
    vitE: 'Vitamina E',
    vitK: 'Vitamina K',
    choline: 'Colina',
    calcium: 'Calcio',
    copper: 'Cobre',
    iron: 'Hierro',
    magnesium: 'Magnesio',
    manganese: 'Manganeso',
    phosphorus: 'Fsforo',
    potassium: 'Potasio',
    selenium: 'Selenio',
    sodium: 'Sodio',
    zinc: 'Zinc',
    water: 'Agua',
    fiber: 'Fibra',
    cholesterol: 'Colesterol',
    saturatedFat: 'Grasas Saturadas',
    monoUnsaturatedFat: 'Grasas Monoinsaturadas',
    polyUnsaturatedFat: 'Grasas Poliinsaturadas',
    glycemicIndex: 'ndice Glicemico',
    comments: 'Informacin Adicional',
  };

  constructor(public bsModalRef: BsModalRef) {
  }

  private _product: IProduct;
  productInfo: any;

  ngOnInit(): void {
  }

  getProductInfo() {
    return Object.keys(this.properties).map(key => {
      return {
        description: this.properties[key],
        amount: this._product[key]
      };
    });
  }
}
