import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { select, Store } from '@ngrx/store';
import * as moment from 'moment';
import { of } from 'rxjs';
import { catchError, concatMap, map, mergeMap, switchMap, withLatestFrom } from 'rxjs/operators';
import * as generateUUID from 'uuid/v4';
import { ApiService, IDiet } from '../../api.service';
import { effectError } from '../app.actions';
import * as dietActions from './diet.actions';
import * as dietSelectors from './diet.selectors';


@Injectable()
export class DietEffects {
  loadDiet$ = createEffect(() =>
    this.actions$.pipe(
      ofType(dietActions.getDietForDay),
      mergeMap((action) => {
        return this.apiSvc.getDiet(action.date)
          .pipe(
            map(diet => {
                if ( !diet ) {
                  const newDiet: IDiet = {
                    id: null, date: action.date.format('YYYY-MM-DD'), meals: {}
                  };
                  const comidas = ['DESAYUNO', 'COMIDA', 'CENA', 'MERIENDA'];

                  const nowMoment = moment();
                  newDiet.meals = comidas.reduce((acc, currentValue, index) => {
                    const id = generateUUID();
                    acc[id] = {
                      id,
                      createdAt: nowMoment.add(index, 'seconds').format('YYYY-MM-DD HH:mm:ss'),
                      description: currentValue,
                      productLines: {}
                    };
                    return acc;
                  }, {});
                  diet = newDiet;

                }
                return dietActions.getDietForDayLoadSuccess({ diet });
              }
            )
          );
      }),
      catchError((e) => of(effectError({ error: `Error al cargar dieta: ${e.message}` })))
    )
  );

  saveDiet$ = createEffect(
    () => this.actions$.pipe(
      ofType(dietActions.saveDiet),
      concatMap(action => of(action).pipe(
        withLatestFrom(this.store$.pipe(select(dietSelectors.selectDiet))))
      ),
      switchMap(([action, diet]) => {
          return this.apiSvc.saveDiet(diet).pipe(
            map((response) => {
              return dietActions.saveDietSuccess({ dietId: response.dietId, correlationId: action.correlationId });
            })
          );
        }
      ),
      catchError((e) => of(effectError({ error: `Error al salvar datos de usuario: ${e.message}` })
        )
      )
    )
  );

  constructor(
    private actions$: Actions,
    private apiSvc: ApiService,
    private store$: Store
  ) {
  }
}
