export interface IUserData {
  userName: string;
  gender: string;
  age: number;
  weight: number;
  height: number;
  activityIndex: number;
  bodyFat: number;
  goal: number;
  proteinRatio: number;
  carbsRatio: number;
  fatRatio: number;
  fiberRatio: number;
  pendingChanges?: boolean;
}

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  products: PromiseLike<IProduct[]>;

  constructor(private http: HttpClient) {
    if ( !environment.production ) {
      this.products = fetch('./assets/products.json')
        .then(response => {
          return response.json();
        })
        .then(products => {
          return products
            .map((product, index) => {
              return {
                id: index + 1,
                ...product
              };
            });
        });
    }
  }

  getProducts(search: string = null, limit = 10): Observable<IProduct[]> {
    if ( !environment.production ) {
      return fromPromise(this.products).pipe(
        map(products => {
          if ( search ) {
            const regex = toRegex({ flags: 'i' })(search);
            products = products.filter(x => x.description.search(regex) !== -1);
          }
          if ( limit ) {
            products = products.slice(0, limit);
          }
          return products.sort((a, b) => a.description.localeCompare(b.description));
        })
      );
    }

    const body = new HttpParams()
      .set('action', 'get_products')
      .set('search', search)
      .set('limit', String(limit));
    return this.http.post<IProduct[]>('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    });
  }

  getUserData(): Observable<IUserData> {
    if ( !environment.production ) {
      const userDataJSON = localStorage.getItem('userData');
      let toReturn = null;
      if ( userDataJSON ) {
        toReturn = JSON.parse(userDataJSON);
      }
      return of(toReturn);
    }

    const body = new HttpParams()
      .set('action', 'get_user_data');
    return this.http.post<IUserData>('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    });
  }

  saveUserData(userData: IUserData): Observable<any> {
    if ( !environment.production ) {
      return of(localStorage.setItem('userData', JSON.stringify(userData)));
    }
    let body = new HttpParams()
      .set('action', 'save_user_data');
    Object.keys(userData).forEach(key => {
      body = body.set(`userData[${key}]`, userData[key]);
    });
    return this.http.post('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    });
  }

  getDiet(date: Moment): Observable<IDiet> {
    if ( !environment.production ) {
      const dietsJSON = localStorage.getItem('diets');
      let toReturn = null;
      if ( dietsJSON ) {
        const diets: IDiet[] = JSON.parse(dietsJSON);
        toReturn = diets.find(x => x.date === date.format('YYYY-MM-DD'));
      }
      return of(toReturn);
    }
    const body = new HttpParams()
      .set('action', 'get_date_diet')
      .set('date', date.format('YYYY-MM-DD'));
    return this.http.post<ServerDiet>('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    }).pipe(
      map((serverDiet) => {
        return this.serverDietToIDiet(serverDiet);
      })
    );
  }

  private getLocalStorageDiets(): IDiet[] {
    const dietsJSON = localStorage.getItem('diets');
    let toReturn: IDiet[] = [];
    if ( dietsJSON ) {
      toReturn = JSON.parse(dietsJSON);
    }
    return toReturn;
  }

  private saveLocalStorageDiets(diets: IDiet[]) {
    localStorage.setItem('diets', JSON.stringify(diets));
  }

  private toSaveDiet(diet: IDiet): any {
    const serverDiet = this.dietToServerDiet(diet);
    return {
      ...serverDiet,
      meals: serverDiet.meals.map(meal => {
        return {
          ...meal, productLines: meal.productLines.map(productLine => {
            const { id, product, amount, createdAt } = productLine;
            return { id, amount, product: { id: product ? product.id : null }, createdAt };
          })
        };
      })
    };
  }

  private dietToServerDiet(diet: IDiet): ServerDiet {
    return {
      id: diet.id, date: diet.date, meals: Object.keys(diet.meals).map(key => {
        const currentMeal = diet.meals[key];
        return {
          id: currentMeal.id,
          description: currentMeal.description,
          createdAt: currentMeal.createdAt,
          productLines: Object.keys(currentMeal.productLines).map(plKey => {
            return currentMeal.productLines[plKey];
          })
        };
      })
    };
  }

  private serverDietToIDiet(diet: ServerDiet): IDiet {
    if ( !diet ) {
      return null;
    }

    return {
      id: diet.id,
      date: diet.date,
      meals: diet.meals.reduce((mealDict, meal) => {
        mealDict[meal.id] = {
          id: meal.id,
          description: meal.description,
          createdAt: meal.createdAt,
          productLines: meal.productLines.reduce((previousValue, currentValue) => {
            previousValue[currentValue.id] = currentValue;
            return previousValue;
          }, {})
        };
        return mealDict;
      }, {})
    };
  }

  saveDiet(diet: IDiet): Observable<any> {
    if ( !environment.production ) {
      const diets = this.getLocalStorageDiets();
      const foundDietIdx = diets.findIndex(x => x.date === diet.date);
      if ( foundDietIdx !== -1 ) {
        const copy = diets.slice();
        copy[foundDietIdx] = diet;
        return of(this.saveLocalStorageDiets(copy));
      }
      return of(this.saveLocalStorageDiets([...diets, diet]));
    }

    // we need to flatten the objects sent to the server....cause we can't send JSON ...for some reason
    const body = new HttpParams()
      .set('action', 'save_date_diet')
      .set('diet', JSON.stringify(this.toSaveDiet(diet)));
    return this.http.post<{ status: string, dietId: number | string }>('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    });
  }

  getPDF(date: any) {
    if ( !environment.production ) {
      return of(null);
    }
    const body = new HttpParams()
      .set('action', 'get_pdf')
      .set('date', date);
    const headers = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');
    return this.http.post(`/wp-admin/admin-ajax.php?cache=${generateUUID()}`, body.toString(), {
      headers,
      responseType: 'blob'
    }).pipe(tap(blob => {
      const a = document.createElement('a');
      document.body.appendChild(a);
      a.style.display = 'none';
      const url = window.URL.createObjectURL(blob);
      a.href = url;
      a.download = 'dietario.pdf';
      a.click();
      window.URL.revokeObjectURL(url);
    }));
  }


  getDietsbyDate(year: number ,  month: number): Observable<ServerDiet[]> {
    if ( !environment.production ) {
      const dietsJSON = localStorage.getItem('diets');
      let toReturn = null;
      // if ( dietsJSON ) {
      //   const diets: IDiet[] = JSON.parse(dietsJSON);
      //   toReturn = diets.find(x => x.date === date.format('YYYY-MM-DD'));
      // }
      return of(toReturn);
    }
    const body = new HttpParams()
      .set('action', 'get_diets_date')
      .set('month', year.toString())
      .set('year', month.toString());
      
    return this.http.post<ServerDiet[]>('/wp-admin/admin-ajax.php', body.toString(), {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded')
    });
  }

}
