import { createAction, props } from '@ngrx/store';
import { IUserData } from '../../api.service';


export const updateAge = createAction('UPDATE_AGE', props<{ value: number }>());
export const updateHeight = createAction('UPDATE_HEIGHT', props<{ value: number }>());
export const updateWeight = createAction('UPDATE_WEIGHT', props<{ value: number }>());
export const updateActivityIndex = createAction('UPDATE_ACTIVITY_INDEX', props<{ value: number }>());
export const updateBodyFatPercentage = createAction('UPDATE_BODY_FAT_PERCENTAGE', props<{ value: number }>());
export const updateGoal = createAction('UPDATE_GOAL', props<{ value: number }>());
export const updateProteinRatio = createAction('UPDATE_PROTEIN_RATIO', props<{ value: number }>());
export const updateCarbsRatio = createAction('UPDATE_CARBS_RATIO', props<{ value: number }>());
export const updateFatRatio = createAction('UPDATE_FAT_RATIO', props<{ value: number }>());
export const updateGender = createAction('UPDATE_GENDER', props<{ value: string }>());
export const getUserData = createAction('GET_USER_DATA');
export const userDataLoadSuccess = createAction('USER_DATA_LOAD_SUCCESS', props<{ userData: IUserData }>());
export const saveUserData = createAction('SAVE_USER_DATA', props<{ correlationId: string }>());
export const saveUserDataSuccess = createAction('SAVE_USER_DATA_SUCCESS', props<{ correlationId: string }>());
