import { createSelector } from '@ngrx/store';
import { AppState } from '../app.state';

export const selectUserData = createSelector((state: AppState) => state.userData, userData => userData);

export const selectBodyMassIndex = createSelector(
  selectUserData,
  (userData) => {
    if ( !userData ) {
      return null;
    }
    if ( !userData.weight || !userData.height ) {
      return null;
    }
    return 10000 * userData.weight / (userData.height * userData.height);
  }
);

export const selectLeanBodyMass = createSelector(
  selectUserData,
  (userData) => {
    if ( !userData ) {
      return null;
    }
    if ( !userData.bodyFat || !userData.weight ) {
      return null;
    }
    return (100 - userData.bodyFat) * userData.weight / 100;
  }
);

export const selectLeanBodyMassIndex = createSelector(
  selectUserData,
  selectLeanBodyMass,
  (userData, leanBodyMass) => {
    if ( !userData.bodyFat || !userData.weight || !userData.height ) {
      return null;
    }
    return (10000 * leanBodyMass / (userData.height * userData.height));
  }
);

export const selectBasalCalories = createSelector(
  selectLeanBodyMass,
  (leanBodyMass) => {
    return 370 + (21.6 * leanBodyMass);
  }
);

export const selectGoalCalories = createSelector(
  selectUserData,
  selectBasalCalories,
  (userData, basalCalories) => {
    if ( !userData.bodyFat || !userData.weight || !userData.activityIndex || !userData.goal ) {
      return null;
    }
    const maintenanceCalories = userData.activityIndex * basalCalories;
    let multiplier;
    const goal = +userData.goal;
    switch (goal) {
      case 1 :
        multiplier = 1;
        break;
      case 2:
        multiplier = 1 - 0.15;
        break;
      case 3:
        multiplier = 1 + 0.10;
    }
    return maintenanceCalories * multiplier;
  }
);
export const selectGoalProteinGrams = createSelector(
  selectUserData,
  selectLeanBodyMass,
  (userData, leanBodyMass) => leanBodyMass * userData.proteinRatio
);

export const selectGoalProteinKCal = createSelector(
  selectGoalProteinGrams,
  proteinGrams => proteinGrams * 4
);

export const selectGoalFatGrams = createSelector(
  selectUserData,
  selectLeanBodyMass,
  (userData, leanBodyMass) => leanBodyMass * userData.fatRatio
);

export const selectGoalFiberGrams = createSelector(
    selectUserData,
    fiberGrams => 20
);

export const selectGoalFatKCal = createSelector(
  selectGoalFatGrams,
  fatGrams => fatGrams * 9
);

export const selectGoalCarbsGrams = createSelector(
  selectUserData,
  selectLeanBodyMass,
  (userData, leanBodyMass) => leanBodyMass * userData.carbsRatio
);

export const selectGoalCarbsKCal = createSelector(
  selectGoalCarbsGrams,
  carbsGrams => carbsGrams * 4
);


export const selectChangesPending = createSelector(
  selectUserData,
  userData => userData.pendingChanges
);
