import {Component, OnDestroy, OnInit, TemplateRef} from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {select, Store} from '@ngrx/store';
import * as jsPDF from 'jspdf';
import {UserOptions} from 'jspdf-autotable';
import 'jspdf-autotable';
import * as  moment from 'moment';
import {Moment} from 'moment';
import {AccordionPanelComponent, BsModalRef, BsModalService} from 'ngx-bootstrap';
import {Observable, Subject, Subscriber, Subscription} from 'rxjs';
import {debounceTime, distinctUntilChanged, filter, startWith, switchMap, tap} from 'rxjs/operators';
import {v4 as generateUUID} from 'uuid';
import {Calculator} from '../utils/calculator';
import {ApiService, IDiet, IMeal, IProduct, ICustomerProduct, IProductLine, IUserData} from './api.service';
import {ProductInfoModalComponent} from './product-info-modal/product-info-modal.component';
import {ProductSearchSelectModalComponent} from './product-search-select-modal/product-search-select-modal.component';
import * as appActions from './store/app.actions';
import {requestGetPDF} from './store/app.actions';
import {
  selectDietRemainingCalories,
  selectDietRemainingCarbsGrams,
  selectDietRemainingFatGrams,
  selectDietRemainingFiberGrams,
  selectDietRemainingProteinGrams
} from './store/app.selectors';
import * as dietActions from './store/diet/diet.actions';
import {
  addMeal,
  addProductLine,
  changeProductLineAmount,
  changeProductLineProduct,
  deleteMeal,
  deleteProductLine,
  updateMealDescription
} from './store/diet/diet.actions';
import * as dietSelectors from './store/diet/diet.selectors';
import * as userDataActions from './store/userData/userData.actions';
import * as userDataSelectors from './store/userData/userData.selectors';

// jquery
import * as $ from 'jquery';
import { CalendarEvent, CalendarEventAction, CalendarView } from 'angular-calendar';
import { endOfDay, isSameDay, isSameMonth, startOfDay } from 'date-fns';
import { EventColor } from 'calendar-utils';

interface JsPDFWithPlugin extends jsPDF {
  autoTable: (options: UserOptions) => jsPDF;
}

const colors: Record<string, EventColor> = {
  red: {
    primary: '#843B94',
    secondary: '#FAE3E3',
  }
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'angular-test';

  storeCustomerProductErrors = [];
  showSuccessMessage = false;
  blockStoreButton = false;

  diet$: Observable<IDiet>;
  userData$: Observable<IUserData>;
  currentDate: moment.Moment;
  productFind$: any;
  onDateChanged$: Subject<Moment> = new Subject<Moment>();
  currentSearch: any;
  modalRef: BsModalRef;
  toEditMeal: IMeal;
  toEditCustomerProduct: ICustomerProduct;
  collapseCustomerDetails: any;
  private toEditDiet: IDiet;
  addingMeal: boolean;
  addingCustomerProduct: boolean;
  userDataForm: FormGroup;
  bodyMassIndex$: Observable<null | number>;
  subscriptions = new Subscription();
  leanBodyMass$: Observable<null | number>;
  leanBodyMassIndex$: Observable<null | number>;
  goalCalories$: Observable<number>;
  proteinKCal$: Observable<number>;
  fatKCal$: Observable<number>;
  carbsKCal$: Observable<number>;
  remainingKCal$: Observable<number>;
  remainingProteinGrams$: Observable<number>;
  proteinGrams$: Observable<number>;
  fatGrams$: Observable<number>;
  fiberGrams$: Observable<number>;
  carbsGrams$: Observable<number>;
  remainingFatGrams$: Observable<number>;
  remainingFiberGrams$: Observable<number>;
  remainingCarbsGrams$: Observable<number>;

  keyValueTrackFunction(index, item) {
    if (!item) {
      return null;
    }
    return item.key;
  }

  // --------------------------
  showCalendar: boolean = false;
  view: CalendarView = CalendarView.Month;
  CalendarView = CalendarView;
  viewDate: Date = new Date();

  actions: CalendarEventAction[] = [
    {
      label: '<i class="fas fa-fw fa-pencil-alt"></i>',
      a11yLabel: 'Edit',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.handleEvent('Edited', event);
      },
    },
    {
      label: '<i class="fas fa-fw fa-trash-alt"></i>',
      a11yLabel: 'Delete',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.events = this.events.filter((iEvent) => iEvent !== event);
        this.handleEvent('Deleted', event);
      },
    },
  ];

  events: CalendarEvent[] = [];

  activeDayIsOpen: boolean = true;
  refresh = new Subject<void>();

  constructor(
    private apiSvc: ApiService,
    private modalSvc: BsModalService,
    private store: Store,
    private formBuilder: FormBuilder,
  ) {

    this.currentDate = moment();

    this.userDataForm = this.formBuilder.group({
      gender: new FormControl(''),
      age: new FormControl(''),
      weight: new FormControl(''),
      height: new FormControl(''),
      activityIndex: new FormControl(''),
      bodyFat: new FormControl(''),
      goal: new FormControl(''),
      proteinRatio: new FormControl(''),
      carbsRatio: new FormControl(''),
      fatRatio: new FormControl(''),
    });

    Object.keys(this.userDataForm.controls).forEach(formControlKey => {
      this.subscriptions.add(
        this.userDataForm.controls[formControlKey].valueChanges.pipe(
          debounceTime(400),
          distinctUntilChanged()
        ).subscribe(value => {
          switch (formControlKey) {
            case'age' :
              this.store.dispatch(userDataActions.updateAge({value}));
              break;
            case 'gender' :
              this.store.dispatch(userDataActions.updateGender({value}));
              break;
            case 'height':
              this.store.dispatch(userDataActions.updateHeight({value}));
              break;
            case 'weight':
              this.store.dispatch(userDataActions.updateWeight({value}));
              break;
            case 'activityIndex':
              this.store.dispatch(userDataActions.updateActivityIndex({value}));
              break;
            case 'bodyFat':
              this.store.dispatch(userDataActions.updateBodyFatPercentage({value}));
              break;
            case 'goal':
              this.store.dispatch(userDataActions.updateGoal({value}));
              break;
            case 'proteinRatio':
              this.store.dispatch(userDataActions.updateProteinRatio({value}));
              break;
            case 'carbsRatio':
              this.store.dispatch(userDataActions.updateCarbsRatio({value}));
              break;
            case 'fatRatio':
              this.store.dispatch(userDataActions.updateFatRatio({value}));
              break;
          }
        }));
    });

    this.bodyMassIndex$ = this.store.pipe(select(userDataSelectors.selectBodyMassIndex));
    this.leanBodyMass$ = this.store.pipe(select(userDataSelectors.selectLeanBodyMass));
    this.leanBodyMassIndex$ = this.store.pipe(select(userDataSelectors.selectLeanBodyMassIndex));
    this.goalCalories$ = this.store.pipe(select(userDataSelectors.selectGoalCalories));
    this.proteinKCal$ = this.store.pipe(select(userDataSelectors.selectGoalProteinKCal));
    this.fatKCal$ = this.store.pipe(select(userDataSelectors.selectGoalFatKCal));
    this.carbsKCal$ = this.store.pipe(select(userDataSelectors.selectGoalCarbsKCal));
    this.proteinGrams$ = this.store.pipe(select(userDataSelectors.selectGoalProteinGrams));
    this.fatGrams$ = this.store.pipe(select(userDataSelectors.selectGoalFatGrams));
    this.fiberGrams$ = this.store.pipe(select(userDataSelectors.selectGoalFiberGrams));
    this.carbsGrams$ = this.store.pipe(select(userDataSelectors.selectGoalCarbsGrams));
    this.remainingKCal$ = this.store.pipe(select(selectDietRemainingCalories));
    this.remainingProteinGrams$ = this.store.pipe(select(selectDietRemainingProteinGrams));
    this.remainingFatGrams$ = this.store.pipe(select(selectDietRemainingFatGrams));
    this.remainingFiberGrams$ = this.store.pipe(select(selectDietRemainingFiberGrams));
    this.remainingCarbsGrams$ = this.store.pipe(select(selectDietRemainingCarbsGrams));


    this.store.dispatch(userDataActions.getUserData());
    this.productFind$ = new Observable((observer: Subscriber<string>) => {
      // Runs on every search
      observer.next(this.currentSearch);
    })
      .pipe(
        switchMap(search => {
          return this.apiSvc.getProducts(search, 10);
        })
      );
  }

  ngOnInit(): void {
    this.userData$ = this.store.pipe(
      select(userDataSelectors.selectUserData),
      tap((userData) => {
        this.userDataForm.patchValue(userData, {emitEvent: false});
      })
    );
    this.diet$ = this.store.pipe(select(dietSelectors.selectDiet));

    this.subscriptions.add(
      this.store.pipe(select(dietSelectors.selectChangesPending), filter(x => !!x)).subscribe(x => {
        this.store.dispatch(dietActions.saveDiet({correlationId: generateUUID()}));
      })
    );

    this.subscriptions.add(
      this.store.pipe(
        select(userDataSelectors.selectChangesPending),
        tap(x => {
          console.log(x);
        }),
        filter(x => !!x)
      ).subscribe(x => {
        this.store.dispatch(userDataActions.saveUserData({correlationId: generateUUID()}));
      })
    );

    this.subscriptions.add(
      this.onDateChanged$.pipe(
        startWith(this.currentDate)
      )
        .subscribe(date => {
          this.store.dispatch(dietActions.getDietForDay({date}));
        })
    );
    this.collapseCustomerDetails = '';
  }

  private modifyDate(amount) {
    this.currentDate = moment(this.currentDate.add(amount, 'days'));
    this.onDateChanged$.next(this.currentDate);
  }

  nextDate() {
    this.modifyDate(+1);
  }

  previousDate() {
    this.modifyDate(-1);
  }

  addMeal(templateRef: TemplateRef<any>) {
    this.modalRef = this.modalSvc.show(templateRef, {class: 'modal-dialog-centered'});
    this.addingMeal = true;
    this.toEditMeal = {id: generateUUID(), description: null, productLines: {}, createdAt: null};
  }

  updateMealDescription() {
    if (this.addingMeal) {
      this.addingMeal = false;
      this.store.dispatch(addMeal({id: this.toEditMeal.id, description: this.toEditMeal.description}));
    } else {
      this.store.dispatch(updateMealDescription({mealId: this.toEditMeal.id, description: this.toEditMeal.description}));
    }
    this.modalRef.hide();
  }

  editMeal($event: MouseEvent, meal: IMeal, diet: IDiet, mealEditModal: TemplateRef<any>) {
    $event.stopPropagation();
    this.toEditMeal = {...meal};
    this.addingMeal = false;
    this.modalRef = this.modalSvc.show(mealEditModal);
  }

  deleteMeal() {
    this.store.dispatch(deleteMeal({mealId: this.toEditMeal.id}));
    this.modalRef.hide();
  }

  save() {
    this.store.dispatch(appActions.requestSaveData());
  }

  addProduct($event: MouseEvent, meal: IMeal, diet: IDiet, accordion: AccordionPanelComponent) {
    $event.stopPropagation();
    if (!accordion.isOpen) {
      accordion.isOpen = true;
    }
    this.store.dispatch(addProductLine({mealId: meal.id}));
  }

  changeProductLine(productLine: IProductLine, meal: IMeal) {
    const modalRef = this.modalSvc.show(ProductSearchSelectModalComponent, {initialState: {product: productLine.product}});
    modalRef.content.onProductSelected = (product) => {
      if (product.id === productLine?.product?.id) {
        return;
      }
      this.store.dispatch(changeProductLineProduct({mealId: meal.id, productLineId: productLine.id, product}));
    };
  }

  getProductLineInfo(productLine: IProductLine, productKey) {
    return (new Calculator()).getProductLineInfo(productLine, productKey);
  }

  getDietInfo(diet: IDiet, key: string) {
    return (new Calculator()).getDietInfo(diet, key);
  }

  getMealInfo(meal: IMeal, key: string) {
    return (new Calculator()).getMealInfo(meal, key);
  }

  getMealEnergyInfo(meal: IMeal, nutrient: string) {
    return (new Calculator()).getMealEnergyInfo(meal, nutrient);
  }

  getMealRecommendedIntakePercent(userData: IUserData, meal: IMeal, nutrient: string) {
    return (new Calculator()).getMealRecommendedIntakePercent(userData, meal, nutrient);
  }

  showInfoModal(templateRef: TemplateRef<any>) {
    this.modalRef = this.modalSvc.show(templateRef);
  }

  showProductInfoModal(product: IProduct, qty: number) {
    this.modalSvc.show(ProductInfoModalComponent, {initialState: {product, qty}});
  }

  // added button
  addCustomerProduct(templateRef: TemplateRef<any>) {
    this.blockStoreButton = false;
    this.showSuccessMessage = false;
    this.modalRef = this.modalSvc.show(templateRef, {class: 'modal-dialog-centered'});
    this.addingCustomerProduct = true;
    this.toEditCustomerProduct = {
      id: generateUUID(),
      description: null,
      amount: 100,
      unit: 'gr',
      kCalories: null,
      protein: null,
      carbohydrates: null,
      fat: null,
      vitA: null,
      vitB1: null,
      vitB2: null,
      vitB3: null,
      vitB5: null,
      vitB6: null,
      vitB9: null,
      vitB12: null,
      vitC: null,
      vitD: null,
      vitE: null,
      vitK: null,
      choline: null,
      calcium: null,
      copper: null,
      iron: null,
      magnesium: null,
      manganese: null,
      phosphorus: null,
      potassium: null,
      selenium: null,
      sodium: null,
      zinc: null,
      water: null,
      fiber: null,
      cholesterol: null,
      saturatedFat: null,
      monoUnsaturatedFat: null,
      polyUnsaturatedFat: null,
      glycemicIndex: null,
      comments: null
    };
  }

  showCustomerProductDetails() {
    this.collapseCustomerDetails = this.collapseCustomerDetails == '' ? 'show' : '';
  }

  storeCustomerProduct() {
    this.storeCustomerProductErrors = [];
    this.blockStoreButton = true;
    $.ajax({
      url: '/wp-json/cca/v1/customer_food',
      data: JSON.stringify({'item': this.toEditCustomerProduct}),
      headers: {
        'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
      },
      method: 'POST',
      dataType: 'JSON',
      processData: false,
      contentType: 'application/json',
      success: (response) => {
        // console.log('response');
        // console.log(response);
        // console.log('response.errors');
        // console.log(response.errors);
        this.storeCustomerProductErrors = response.errors;
        if (this.storeCustomerProductErrors.length == 0) {
          this.showSuccessMessage = true;
          // to reload data
          this.save(); // candidate
          setTimeout(() => {
            this.modalRef.hide();
          }, 4000);
        }else{
          // on errors found the button must be re-enabled
          this.blockStoreButton = false;
        }
      },
      error: function(http, status, error) {
        console.log('http', http);
        console.log('status', status);
        console.log('error', error);
      }
    });
  }

  deleteProductLine(productLine: IProductLine, meal: IMeal) {
    this.store.dispatch(deleteProductLine({productLineId: productLine.id, mealId: meal.id}));
  }

  exportToPDF(diet: IDiet) {
    this.store.dispatch(requestGetPDF({date: diet.date}));
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  changeProductLineAmount(meal: IMeal, productLine: IProductLine, amount: any) {
    this.store.dispatch(changeProductLineAmount({mealId: meal.id, productLineId: productLine.id, amount: +amount}));
  }


  public handleEvent(action: string, event: CalendarEvent): void {
    console.log(action, event, "handleEvent")
  }

  public dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    console.log(date, "date??????????????")
    console.log( moment( date ) )
    this.currentDate = moment( date );
    this.onDateChanged$.next(this.currentDate);
    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
        console.log(false, "date??????????????")


      } else {
        this.activeDayIsOpen = true;
        console.log(true, "date??????????????")
      }
      this.viewDate = date;
    }
  } 


  public closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
    console.log( this.viewDate , "this.viewDate")
    const month = parseInt(moment(this.viewDate).format("M"));
    const year = parseInt(moment(this.viewDate).format("Y"));
    this.listEventsByMonth(month, year)
  }

  addEvent(): void {
    this.events = [
      ...this.events
    ];
  }


  deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter((event) => event !== eventToDelete);
  }

  setView(view: CalendarView) {
    this.view = view;
  }

  listEventsByMonth( month: number , year: number): void{

    console.log( new Date( moment("2028-12-20").toISOString() ))
    console.log(month, year)

    this.apiSvc.getDietsbyDate( month, year).subscribe(  
      (res) => {
        console.log(res);

        this.events = [];
        for (let index = 0; index < res.length; index++) {
          const element = res[index];

          this.events.push({
            title: element.date,
            start: startOfDay(new Date( moment(element.date).toISOString() )),
            end: endOfDay(new Date( moment(element.date).toISOString() )),
            color: colors.red,
            draggable: true,
            resizable: {
              beforeStart: true,
              afterEnd: true,
            },
          },)
        }
      }
    )
  }

}
