import { IDiet, IMeal, IProductLine, IUserData } from '../app/api.service';
import {
  selectGoalCalories,
  selectGoalCarbsGrams,
  selectGoalFatGrams,
  selectGoalProteinGrams
} from '../app/store/userData/userData.selectors';

export class Calculator {
  getDietInfo(diet: IDiet, key: string) {
    return Object.values(diet.meals).reduce((acc, meal) => acc + this.getMealInfo(meal, key), 0);
  }

  getMealEnergyInfo(meal: IMeal, nutrient: string) {
    const protein = this.getMealInfo(meal, 'protein');
    const carbohydrates = this.getMealInfo(meal, 'carbohydrates');
    const fat = this.getMealInfo(meal, 'fat');
    const fiber = this.getMealInfo(meal, 'fiber');

    if ( (fat + carbohydrates + protein) === 0 ) {
      return 0;
    }
    let numerator;
    switch (nutrient) {
      case 'fat':
        numerator = fat * 9;
        break;
      case 'protein':
        numerator = protein * 4;
        break;
      case 'carbohydrates':
        numerator = carbohydrates * 4;
        break;
      case 'fiber':
        numerator = fiber * 0;
        break;
    }
    return numerator / (protein * 4 + carbohydrates * 4 + fat * 9);
  }

  getMealRecommendedIntakePercent(userData: IUserData, meal: IMeal, nutrient: string) {
    switch (nutrient) {
      case 'kCalories':
        const totalCalories = selectGoalCalories({ userData, diet: null });
        return this.getMealInfo(meal, nutrient) / totalCalories;
      case 'protein':
        const proteinGrams = selectGoalProteinGrams({ userData, diet: null });
        return this.getMealInfo(meal, nutrient) / proteinGrams;
      case 'carbohydrates':
        const carbohydrates = selectGoalCarbsGrams({ userData, diet: null });
        return this.getMealInfo(meal, nutrient) / carbohydrates;
      case 'fat':
        const fat = selectGoalFatGrams({ userData, diet: null });
        return this.getMealInfo(meal, nutrient) / fat;
      case 'fiber':
        const fiber = selectGoalFatGrams({ userData, diet: null });
        return this.getMealInfo(meal, nutrient) / fiber;
    }
  }

  getProductLineInfo(productLine: IProductLine, productKey) {
    if ( !productLine.product ) {
      return null;
    }
    return (productLine.product[productKey] / productLine.product.amount) * productLine.amount;
  }

  getMealInfo(meal: IMeal, key: string) {
    return Object.values(meal.productLines).reduce((acc, pl) => {
      acc += this.getProductLineInfo(pl, key);
      return acc;
    }, 0);
  }
}

export const getNutrientByWeightRatio = (weight, ratio) => weight * 2.2 * ratio;
export const getRatioByWeightAndAmount = (leanBodyMass, amount) => +(amount / leanBodyMass).toFixed(2);

export const proteinGramsToKCal = x => x * 4;
export const carbsGramsToKCal = x => x * 4;
export const fatGramsToKCal = x => x * 9;

export const proteinKCalToGrams = x => x / 4;
export const carbsKCalToGrams = x => x / 4;
export const fatKCalToGrams = x => x / 9;

