import { createAction, props } from '@ngrx/store';

export const requestGetPDF = createAction('REQUEST_GET_PDF', props<{ date: string }>());
export const getPDF = createAction('GET_PDF', props<{ date: string, correlationId: string }>());
export const effectError = createAction('SAVE_ERROR', props<{ error: string }>());
export const requestSaveData = createAction('REQUEST_SAVE_DATA');
export const saveData = createAction('SAVE_DATA', props<{ correlationId }>());
export const saveDataSuccess = createAction('SAVE_DATA_SUCCESS', props<{ correlationId?: string }>());
export const dietChanged = createAction('DIET_CHANGED');
export const failedMiserably = createAction('FAILED_MISERABLY');
