import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { combineLatest, EMPTY, of, zip } from 'rxjs';
import { flatMap, map, mergeMap, switchMap } from 'rxjs/operators';
import { v4 as generateUUID } from 'uuid';
import { filterCorrelatedId } from '../../utils/filterCorrelated';
import { ApiService } from '../api.service';
import * as appActions from './app.actions';
import { saveDataSuccess } from './app.actions';
import { AppState } from './app.state';
import * as dietActions from './diet/diet.actions';
import { DietEffects } from './diet/diet.effects';
import * as userDataActions from './userData/userData.actions';
import { UserDataEffects } from './userData/userData.effects';

@Injectable()
export class AppEffects {
  constructor(
    private actions$: Actions,
    private store$: Store<AppState>,
    private userDataEffects: UserDataEffects,
    private dietEffects: DietEffects,
    private apiSvc: ApiService
  ) {
  }

  requestSaveData$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.requestSaveData),
      map(() => {
        return appActions.saveData({ correlationId: generateUUID() });
      })
    )
  );

  saveData$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.saveData),
      flatMap((action) => {
        return [
          userDataActions.saveUserData({ correlationId: action.correlationId }),
          dietActions.saveDiet({ correlationId: action.correlationId })
        ];
      })
    )
  );

  saveDataSuccess$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.saveData),
      flatMap((action) => zip(
        this.actions$.pipe(ofType(userDataActions.saveUserDataSuccess), filterCorrelatedId(action.correlationId)),
        this.actions$.pipe(ofType(dietActions.saveDietSuccess), filterCorrelatedId(action.correlationId))
        ).pipe(
        map(pair => appActions.saveDataSuccess({ correlationId: action.correlationId }))
        )
      )
    )
  );
  requestGetPDF$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.requestGetPDF),
      mergeMap(action => {
        const correlationId = generateUUID();
        return [
          appActions.getPDF({ correlationId, date: action.date }),
          appActions.saveData({ correlationId })
        ];
      })
    )
  );


  getPDF$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.requestGetPDF),
      switchMap(action => this.actions$.pipe(
        ofType(appActions.getPDF),
        switchMap(
          getPDFAction => combineLatest(
            [of(getPDFAction), this.actions$.pipe(ofType(saveDataSuccess), filterCorrelatedId(getPDFAction.correlationId))
            ]
          )
        ),
        switchMap(() => {
          return this.apiSvc.getPDF(action.date);
        })
        )
      )
    )
  );
  handleError$ = createEffect(
    () => this.actions$.pipe(
      ofType(appActions.effectError),
      mergeMap((action) => {
          alert(action.error);
          return EMPTY;
        }
      ),
    )
  );


}
