import { createAction, props } from '@ngrx/store';
import { Moment } from 'moment';
import { IDiet, IProduct } from '../../api.service';

export const getDietForDay = createAction('GET_DIET_FOR_DAY', props<{ date: Moment }>());
export const getDietForDayLoadSuccess = createAction('GET_DIET_FOR_DAY_LOAD_SUCCESS', props<{ diet: IDiet }>());
export const saveDiet = createAction('SAVE_DIET', props<{ correlationId: string }>());
export const saveDietSuccess = createAction('SAVE_DIET_SUCCESS', props<{ dietId: string | number, correlationId: string }>());
export const addMeal = createAction('ADD_MEAL', props<{ id: string | number, description: string }>());
export const updateMealDescription = createAction('UPDATE_MEAL_DESCRIPTION', props<{ mealId: string | number, description: string }>());
export const deleteMeal = createAction('DELETE_MEAL', props<{ mealId: string | number }>());
export const addProductLine = createAction('ADD_PRODUCT_LINE', props<{ mealId: string | number }>());
// tslint:disable-next-line:max-line-length
export const deleteProductLine = createAction('PRODUCT_LINE_DELETED', props<{ productLineId: number | string, mealId: number | string }>());
// tslint:disable-next-line:max-line-length
export const changeProductLineAmount = createAction('CHANGE_PRODUCT_LINE_AMOUNT', props<{ mealId: number | string, productLineId: number | string, amount: number }>());
// tslint:disable-next-line:max-line-length
export const changeProductLineProduct = createAction('CHANGE_PRODUCT_LINE_PRODUCT', props<{ product: IProduct, productLineId: string | number, mealId: string | number }>());
