import { createSelector } from '@ngrx/store';
import { identity } from 'rxjs';
import { AppState } from '../app.state';

export const selectDiet = createSelector((state: AppState) => {
  return state.diet;
}, identity);


export const selectChangesPending = createSelector(
  selectDiet,
  (diet => {
    return diet.pendingChanges;
  })
);
