import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { select, Store } from '@ngrx/store';
import { of } from 'rxjs';
import { catchError, concatMap, map, mergeMap, switchMap, withLatestFrom } from 'rxjs/operators';
import { ApiService } from '../../api.service';
import { effectError } from '../app.actions';
import { getUserData, saveUserData, saveUserDataSuccess, userDataLoadSuccess } from './userData.actions';
import * as userDataSelectors from './userData.selectors';


@Injectable()
export class UserDataEffects {
  loadUserData$ = createEffect(() =>
    this.actions$.pipe(
      ofType(getUserData),
      mergeMap(() => this.apiSvc.getUserData()),
      map(userData => userDataLoadSuccess({ userData })),
      catchError((e) => of(effectError({ error: `Error al cargar datos de usuario: ${e.message}` })))
    )
  );

  saveUserData$ = createEffect(
    () => this.actions$.pipe(
      ofType(saveUserData),
      concatMap(action => of(action).pipe(
        withLatestFrom(this.store$.pipe(select(userDataSelectors.selectUserData)))
        )
      ),
      switchMap(([action, userData]) => {
          return this.apiSvc.saveUserData(userData).pipe(map(() => saveUserDataSuccess({ correlationId: action.correlationId })));
        }
      ),
      catchError((e) => of(effectError({ error: `Error al salvar datos de usuario: ${e.message}` })
        )
      )
    )
  );

  constructor(
    private actions$: Actions,
    private apiSvc: ApiService,
    private store$: Store
  ) {
  }
}
