import { Action, createReducer, on } from '@ngrx/store';
import * as moment from 'moment';
import { v4 as generateUUID } from 'uuid';
import { IDiet } from '../../api.service';
import { initialState } from '../app.state';

import {
  addMeal,
  addProductLine,
  changeProductLineAmount,
  changeProductLineProduct,
  deleteMeal,
  deleteProductLine,
  getDietForDayLoadSuccess,
  saveDietSuccess,
  updateMealDescription
} from './diet.actions';

const dietReducer = createReducer(initialState.diet,
  on(getDietForDayLoadSuccess, (state, action) => {
    return { ...state, ...action.diet } as IDiet;
  }),
  on(saveDietSuccess, (state, action) => {
    const newState = { ...state, pendingChanges: false };
    if ( state.id !== action.dietId ) {
      return { ...newState, id: action.dietId };
    }
    return newState;
  }),
  on(addMeal, (state, action) => {
    return {
      ...state,
      meals: {
        ...state.meals,
        [action.id]: { id: action.id, description: action.description, productLines: {}, createdAt: moment().format('YYYY-MM-DD HH:mm:ss') }
      },
      pendingChanges: true,
    };
  }),
  on(updateMealDescription, (state, action) => {
    return {
      ...state,
      meals: updateItemInDictionary(state.meals, action.mealId, (mealToUpdate) => {
        return { ...mealToUpdate, description: action.description };
      }),
      pendingChanges: true,
    };
  }),
  on(deleteMeal, (state, action) => {
    return {
      ...state,
      meals: deleteFromEntityState(state.meals, action.mealId),
      pendingChanges: true,
    };
  }),
  on(addProductLine, (state, action) => {
    const newID = generateUUID();
    return {
      ...state,
      meals: updateItemInDictionary(state.meals, action.mealId, (mealToUpdate) => {
          return {
            ...mealToUpdate,
            productLines: {
              ...mealToUpdate.productLines,
              [newID]: { id: newID, product: null, amount: 0, createdAt: moment().format('YYYY-MM-DD HH:mm:ss') }
            }
          };
        }
      ),
      pendingChanges: true,
    };
  }),
  on(deleteProductLine, (state: IDiet, action) => {
    return {
      ...state,
      meals: {
        ...state.meals,
        [action.mealId]: {
          ...state.meals[action.mealId],
          productLines: deleteFromEntityState(state.meals[action.mealId].productLines, action.productLineId)
        }
      },
      pendingChanges: true,
    };
  }),
  on(changeProductLineAmount, (state, action) => {
    return {
      ...state,
      meals: {
        ...state.meals,
        [action.mealId]: {
          ...state.meals[action.mealId],
          productLines: {
            ...state.meals[action.mealId].productLines,
            [action.productLineId]: {
              ...state.meals[action.mealId].productLines[action.productLineId],
              amount: action.amount
            }
          }
        }
      },
      pendingChanges: true,
    };
  }),
  on(changeProductLineProduct, (state, action) => {
    return {
      ...state,
      meals: {
        ...state.meals,
        [action.mealId]: {
          ...state.meals[action.mealId],
          productLines: {
            ...state.meals[action.mealId].productLines,
            [action.productLineId]: {
              ...state.meals[action.mealId].productLines[action.productLineId],
              product: action.product
            }
          }
        }
      },
      pendingChanges: true,
    };
  }),
);

export function reducer(state: IDiet | undefined, action: Action) {
  return dietReducer(state, action);
}

export interface ItemWithId {
  id: string | number;
}

export function deleteFromEntityState(entityState: any, id: string | number) {
  return Object.keys(entityState).reduce((acc, key) => {
    // tslint:disable-next-line:triple-equals
    if ( key != id ) {
      acc[key] = entityState[key];
    }
    return acc;

  }, {});
}

function updateItemInDictionary<T extends ItemWithId>(dictionary: { [id: string]: T },
                                                      itemId: string | number,
                                                      updateItemCallback: (item: T) => T
) {
  return { ...dictionary, [itemId]: updateItemCallback(dictionary[itemId]) };
}
