import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { Observable, of, Subject } from 'rxjs';
import { debounceTime, map, switchMap } from 'rxjs/operators';
import { ApiService, IProduct } from '../api.service';

@Component({
  selector: 'app-product-search-select-modal',
  templateUrl: './product-search-select-modal.component.html',
  styleUrls: ['./product-search-select-modal.component.css']
})
export class ProductSearchSelectModalComponent implements OnInit {

  constructor(public bsModalRef: BsModalRef, private apiSvc: ApiService) {
  }

  public onProductSelected: (product: IProduct) => void;
  public product: IProduct;
  foundItems$: Observable<IProduct[]>;
  searchTermChanged: Subject<string> = new Subject<string>();

  ngOnInit(): void {
    this.foundItems$ = this.searchTermChanged.pipe(
      debounceTime(500),
      switchMap(searchTerm => {
        if ( !searchTerm ) {
          return of([]);
        }
        return this.apiSvc.getProducts(searchTerm, 20);
      }),
      map(products => {
        if ( this.product ) {
          return products.filter(x => x.id !== this.product.id);
        }
        return products;
      })
    );
  }

  onProductSearchChange($event: any) {
    this.searchTermChanged.next($event);
  }

  productSelected(iProduct: IProduct) {
    this.bsModalRef.hide();
    this.onProductSelected(iProduct);
  }
}
