import { createSelector } from '@ngrx/store';
import { Calculator } from '../../utils/calculator';
import { AppState } from './app.state';
import { selectDiet } from './diet/diet.selectors';
import { selectGoalCalories, selectGoalCarbsGrams, selectGoalFatGrams, selectGoalProteinGrams } from './userData/userData.selectors';

export const selectPDFBlob = createSelector(
  ((state: AppState) => state.blobPDFRequest),
  (blobState => blobState.pdfBlob)
);

export const selectDietRemainingCalories = createSelector(
  selectGoalCalories,
  selectDiet,
  (goalCalories, diet) => {
    if ( !goalCalories ) {
      return 0;
    }
    return goalCalories - (new Calculator()).getDietInfo(diet, 'kCalories');
  }
);

export const selectDietRemainingProteinGrams = createSelector(
  selectGoalProteinGrams,
  selectDiet,
  (goalProteinGrams, diet) => {
    if ( !goalProteinGrams ) {
      return 0;
    }
    return goalProteinGrams - (new Calculator()).getDietInfo(diet, 'protein');
  }
);

export const selectDietRemainingFatGrams = createSelector(
  selectGoalFatGrams,
  selectDiet,
  (goalProteinGrams, diet) => {
    if ( !goalProteinGrams ) {
      return 0;
    }
    return goalProteinGrams - (new Calculator()).getDietInfo(diet, 'fat');
  }
);

export const selectDietRemainingFiberGrams = createSelector(
    selectGoalFatGrams,
    selectDiet,
    (goalFiberGrams, diet) => {
        return 20 - (new Calculator()).getDietInfo(diet, 'fiber');
        // if ( !goalFiberGrams ) {
        //     return 0;
        // }
    }
);

export const selectDietRemainingCarbsGrams = createSelector(
  selectGoalCarbsGrams,
  selectDiet,
  (goalProteinGrams, diet) => {
    if ( !goalProteinGrams ) {
      return 0;
    }
    return goalProteinGrams - (new Calculator()).getDietInfo(diet, 'carbohydrates');
  }
);


